*{margin: 0; padding: 0; box-sizing: border-box;}
li{list-style: none;}
a{text-decoration: none;}
img{display: block; border: none;}
button{background: none; border: none; cursor: pointer;}
.app{
  margin: 100px auto;
  width: 1200px;
}
.app>h1{
  font-size: 24px;
}
.app>p{
  font-size: 16px;
  margin-bottom: 45px;
}
.app .wrap{
  width: 100%;
  display: flex;
  justify-content: space-between;
}