.list{
    width: 282px;
}
.list .imgbox{
    width: 100%;
    height: 210px;
    background: gold;
}
.list .imgbox img{
    width: 100%;
    height: 100%;
}
.list .info{
    width: 100%;
    padding: 18px 0;
}
.list .info>p{
    color: #7079a4;
    margin-bottom: 12px;
}
.list .info>h3{
    width: 100%;
    height: 50px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    letter-spacing: -1px;
    margin-bottom: 27px;
}
.list .info>h2{
    font-size: 22px;
}
.list .info>h2>span{
    font-size: 16px;
}